.headerBlock {
  width: 100%;
  height: 80px;
  background-color: #f8f9fa;
  position: fixed;
  z-index: 10;
  box-shadow: 0 3px 4px rgba(0,0,0,.2);
}

.header–°ontainer {
  display: flex;
  background-color: #f8f9fa;
  align-items: center;
  justify-content: space-between;

  .burgerMenu {
    display: none;
    @media (max-width: 785px) {
      display: flex;
      align-items: center;
      margin-left: 30px;
    }
  }

}

.headerLinks {
  display: flex;

  .link {
    display: flex;
    align-items: center;
    height: 70px;
    padding: 0 8px;
    text-decoration: none;;
    cursor: pointer;
    @media (max-width: 610px) {
      svg {
        height: 30px;
        width: 30px;
        transition-duration: 0.3s;
        cursor: pointer;
      }
    }
  }
}

